<template>
  <v-container style="margin-top: 65px">
    <!-- ---------------------------------------------------- -->
    <!--                 Renderização dos card                -->
    <!-- ---------------------------------------------------- -->
    <v-row v-for="(item, index) in ListImg" :key="item.sha">
      <ItensCertificados :item="item" :index="index" />
    </v-row>
  </v-container>
</template>

<script>
import ItensCertificados from "../components/ItensCertificados.vue";
export default {
  name: "ProjetosPage",
  components: {
    ItensCertificados,
  },
  data: () => ({
    ListImg: [],
  }),
  async mounted() {
    if (this.$store.state.repositorios < 1) {
      await this.$store.dispatch("getImagens");
    }
    this.$store.state.imagensCertificados.map((item) => {
      let objeto = {
        imagem: item.download_url,
        nome: item.name.split(".")[0],
        id: item.sha,
      };
      this.ListImg.push(objeto);
    });
  },
};
</script>
