<template>
  <v-col>
    <v-hover v-slot="{ isHovering, props }">
      <v-card
        :elevation="isHovering ? 12 : 2"
        class="mx-auto"
        v-bind="props"
        :style="setColor(index)"
      >
        <v-card-content class="cellList">
          <v-col class="cellList">
            <h4>{{ item.nome }}</h4>
            <span>{{ item.sha }}</span>
          </v-col>
          <v-col class="cellList">
            <div>
              <v-dialog v-model="dialog">
                <template v-slot:activator="{ props }">
                  <v-img
                    class="cellList"
                    v-bind="props"
                    :src="item.imagem"
                    contain
                    width="5rem"
                    heigth="5rem"
                  />
                </template>

                <v-card>
                  <v-img
                    v-bind="props"
                    :src="item.imagem"
                    contain
                    :style="CheckWindow()"
                  />
                  <v-card-actions>
                    <v-btn color="primary" block @click="dialog = false"
                      >Fechar</v-btn
                    >
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </div>
          </v-col>
        </v-card-content>
      </v-card>
    </v-hover>
  </v-col>
</template>
<script>
export default {
  name: "itensCertificados",
  props: {
    item: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
  },
  data: () => ({
    dialog: false,
    larguraTela: "23rem",
  }),
  methods: {
    setColor(index) {
      if (index % 2 === 0) {
        return "background-color: hsla(244, 100%, 50%, 0.103)";
      } else return "background-color: white";
    },
    CheckWindow() {
      if (window.screen.width < 490) {
        return "width: 20rem";
      } else if (window.screen.width >= 490 && window.screen.width <= 800) {
        return "width: 26rem";
      } else if (window.screen.width >= 801) {
        return "width: 30rem";
      }
    },
  },
};
</script>
<style>
.cellList {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
</style>
